<script setup>
    import { ref } from 'vue';
    import { useRouter } from 'vue-router';
    import { useStore } from 'vuex';
    import post from '../../utils/post';

    const store = useStore();
    const router = useRouter();

    const selectedIndex = ref(0);

    function changeCharacter(side){
        if(side === 'left'){
            if(selectedIndex.value > 0) {
                selectedIndex.value -= 1;
                post("SwitchCharacter", { Passport: Math.abs(store.state.chars[selectedIndex.value].Passport) });
                return;
            }
        } else if(side === 'right'){
            if(selectedIndex.value < store.state.chars.length - 1) {
                selectedIndex.value += 1;
                post("SwitchCharacter", { Passport: Math.abs(store.state.chars[selectedIndex.value].Passport) });
                return;
            }
        }

    }

    async function selectCharacter(){
        const res = await post("CharacterChosen", { Passport: Math.abs(store.state.chars[selectedIndex.value].Passport) })
        if(res){
            router.push('/location');
        }
    }

    const genres = {
        'F': 'Feminino',
        'M': 'Masculino'
    }
</script>

<template>
    <div id="selection">
        <div id="container-selection">
            <div class="background-one-selection"></div>
            <p class="text-bottom-selection">Quer um novo personagem ? <span @click="router.push('/create')">Criar agora</span></p>

            <div class="content-container-selection">
                <div class="header-container-selection">
                    <p>SELEÇÃO</p>
                    <p>DE PERSONAGEM</p>
                </div>
                <div class="infos-container-selection" v-if="store.state.chars[selectedIndex]?.Passport">
                    <div class="info-frame-selection">
                        <div class="icon-frame-info-selection">
                            <img src="./images/user.svg" />
                        </div>
                        <p>{{store.state.chars[selectedIndex]?.Nome}}</p>
                    </div>
                    <div class="info-frame-selection">
                        <div class="icon-frame-info-selection">
                            <img src="./images/passport.svg" />
                        </div>
                        <p>{{store.state.chars[selectedIndex]?.Passport}}</p>
                    </div>
                    <div class="info-frame-selection">
                        <div class="icon-frame-info-selection">
                            <img src="./images/genre.svg" />
                        </div>
                        <p>{{genres[store.state.chars[selectedIndex]?.Sexo]}}</p>
                    </div>
                    <div class="info-frame-selection">
                        <div class="icon-frame-info-selection">
                            <img src="./images/blood.svg" />
                        </div>
                        <p>{{store.state.chars[selectedIndex]?.Blood}}</p>
                    </div>
                    <div class="info-frame-selection">
                        <div class="icon-frame-info-selection">
                            <img src="./images/bank.svg" />
                        </div>
                        <p>{{parseFloat(store.state.chars[selectedIndex]?.Banco).toLocaleString('en-US',{style: 'currency', currency: 'USD'})}}</p>
                    </div>
                </div>
            </div>
            <div class="container-character-selection">
                <div class="arrow-character-selection" v-if="store.state.chars[selectedIndex]?.Passport" @click="changeCharacter('left')" :class="{'disabled-arrow': selectedIndex === 0}"></div>
                <div class="arrow-character-selection" v-if="store.state.chars[selectedIndex]?.Passport" @click="changeCharacter('right')" :class="{'disabled-arrow': selectedIndex === store.state.chars.length - 1 }"></div>
                <button @click="selectCharacter" v-if="store.state.chars[selectedIndex]?.Passport">SELECIONAR</button>
            </div>
        </div>
    </div>
</template>

<style scoped>
    #selection {
        width: 100vw;
        height: 100vh;
    }

    #container-selection {
        width: 59.32291666666667vw;
