        transition: all ease .4s;
    }

    .text-bottom-selection span:hover {
        opacity: .7;
    }

    .text-bottom-selection span:active {
        opacity: 1;
    }

    .container-character-selection {
        width: 48.59375vw;
        height: 100%;
        position: absolute;
        transform: translate(0, -50%);
        top: 50%;
