        background-size: 100% 100%;
    }

    .arrow-character-selection:nth-child(1) {
        background-image: url('./images/arrow-left.svg');
    }

    .arrow-character-selection:nth-child(1):not(.disabled-arrow):hover {
        background-image: url('./images/arrow-left-active.svg');
    }

    .arrow-character-selection:nth-child(1):active {
        background-image: url('./images/arrow-left.svg');
    }


    .arrow-character-selection:nth-child(2) {
