        background: linear-gradient(92.51deg, #00BC7E -5.33%, #009D69 100.68%);
        border-radius: 0.36458333333333337vw;
        border: none;
        outline: none;
        font-weight: 600;
        font-size: 1.25vw;
        color: #FFF;
        cursor: pointer;
        transition: all ease .4s;
    }

    .container-character-selection button:hover {
        box-shadow: 0px 0px 4.635416666666667vw rgba(0, 188, 126, 0.7);
    }

    .container-character-selection button:active {
        box-shadow: 0px 0px 0 rgba(0, 188, 126, 0.7);
    }

    .disabled-arrow {
        opacity: .2;
        cursor: default;
    }
</style>